@import "../../global";

.hero-wrapper {
  background-size: cover;
  height: 100vh;
  .hero {
    display: flex;
    justify-items: center;
    color: $text-white;
    height: 100vh;
    background: linear-gradient(
      71.59deg,
      darken($primary, 20) 29.6%,
      rgba(43, 100, 55, 0.4) 100%
    );
  }
  .col-6 {
    width: 60%;
    margin-top: 36vh;
    .hero-btn{
      margin-top: 2rem ;
      button{
        margin-right: 1.5rem;
        text-align: center;
      }
    }
    p {
      line-height: 1.5rem;
      margin-top: 1rem;
    }
  }
}

// responsive tablet
@media only screen and (max-width: 820px) {
  .hero-wrapper {
      .hero{
          background: linear-gradient(
    71.59deg,
    darken($primary, 20) 40%,
    rgba(43, 100, 55, 0.4) 100%
  );
      }
      .col-6 {
        width: 68% ;
        .hero-btn{
          margin-top: 2rem ;
          display: flex;
          flex-direction: column;
          width: 70%;

          button{
            margin-top: 1.5rem;
            margin-right: 0;
            display: flex;
            text-align: center;
            justify-content: center;
          }}
        h1{
            font-size: 2rem;
        }
        p {
          line-height: 1.5rem;
          margin-top: 1rem;
        }
      }
    }

}


// responsive handphone
@media only screen and (max-width: 500px) {
    .hero-wrapper {
        .hero{
            background: linear-gradient(
      71.59deg,
      darken($primary, 20) 40%,
      rgba(43, 100, 55, 0.4) 100%
    );
        }
        .col-6 {
          width: 100% ;
          .hero-btn{
            margin-top: 2rem ;
            display: flex;
            flex-direction: column;
            width: 100%;
  
            button{
              margin-right: 0;
              margin-top: 1rem;
              display: flex;
              text-align: center;
              justify-content: center;
            }}
          h1{
              font-size: 2rem;
          }
          p {
            line-height: 1.5rem;
            margin-top: 1rem;
          }
        }
      }

}
